import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MoneyControlComponent } from './money-control.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular'; 
import { BillDetailModule } from 'src/app/sales/components/bill-detail/bill-detail.module';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

const routes: Routes = [
  {
    path: "",
    component: MoneyControlComponent
  }, 
]

@NgModule({
  declarations: [
    MoneyControlComponent, 
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    FormsModule,
    IonicModule,
    ReactiveFormsModule,
    BillDetailModule
  ],
  providers: [VhComponent]
})
export class MoneyControlModule { }
